// alpha-beta-pruning with negaMax
function negaMax(grid, player, depth, alpha, beta) {
    let bestMove;
    if (depth === 0 || endPosition(grid))
        return value(grid, player);
    let maxVal = alpha;
    let moves = generateMoves(grid);
    for (let move of moves) {
        makeMove(grid, move, player);
        let val = -negaMax(grid, -player, depth - 1, -beta, -maxVal);
        revertMove(grid, move, player);
        if (val > maxVal) {
            maxVal = val;
            if (depth === MAXDEPTH)
                bestMove = { ...move};
            if (maxVal >= beta)
                break;
        }
    }
    return maxVal;
}

