import '../style/connectFour.css';
import { useState } from 'react';
import { aiMove } from './logic';

// npm run deploy
// npm start


function Cell(props) {
  let classname;
  if (props.value === 1) classname = 'p1';
  else if (props.value === -1) classname = 'p2';

  return (
      <div>
        <div className={'cell'}><div className={classname}></div>
      </div></div>
        
  )
}

function Board(props) {
  return (
      <div className={"board"}>
          {props.grid.map((col, i) => 
              <div className={"column-box"}>
                <div key={i} id={`col-${i}`} className={"col"} >
                    {col.map((value, j) => <Cell key={j} value={value}/>)}
                </div>
                <div key={i+"rand"} className={"col-border"} 
                  onClick={() => props.onClick(i)}
                >
                  {col.map((value, j) => <div key={j} className={"cell-border"}/>)}

                </div>
              </div>
          )}
      </div>
  )
}


export default function ConnectFour(props) {
    const [grid, setGrid] = useState( createGrid() );
    const [pNext, setNext] = useState( true );
    const [gameState, setGame] = useState(0);
    const [gameActive, setActive] = useState(false);

    function makeMove(i, g, p) {
      if (g[i][0] || gameOver(g)) {
        console.log("col full");
        
        return;
      }
      let gridCopy = [];
      for (let j = 0; j < g.length; j++)
        gridCopy[j] = g[j].slice();


      let row = 6;
      for( let j = 0; j < 6; j++) {
        if(gridCopy[i][j]) {
          row = j;
          break;
        }
      }
      gridCopy[i][row-1] = p ? 1 : -1;
      
      console.log("move at" + i + row);
      setGame(gameOver(gridCopy));
      setGrid(gridCopy)
      return gridCopy;
    }

    async function handleClick(i) {
      if( gameActive && gameState === 0 && pNext) {
        setNext(false);
        let nGrid = makeMove(i, grid, true);
        let gridCopy = [];
        for (let j = 0; j < nGrid.length; j++)
          gridCopy[j] = nGrid[j].slice();
        await new Promise(r => setTimeout(r, 550));
        aiMove(gridCopy).then(
            result => {
              makeMove(result, nGrid, false);
              setNext(true);
            }
        )

      }
    }
    
    function handleResetClick(e) {
      
      e.preventDefault();
      if (gameActive) {   
        setGrid(createGrid());
        setGame(0);
        setActive(false);
      }
      else {
        setActive(true);
        if (Math.random() < 0.5) {
          setNext(true);
        }
        else {
          setNext(false);
          aiMove(createGrid()).then(
            result => {
              makeMove(result, createGrid(), false);
              setNext(true);
            });
        }
      }
    }

    return (
      <div className={"background"}>
        <div className={"connect-four"}>
          <div className={"header"}>
            <h1>CONNECT FOUR AI</h1>
            <h2>made by jannis becketepe and timon kobusch.</h2>
          </div>
          <div className={"menu"}>
            <button className="game-button" onClick={handleResetClick}>{gameActive ? 'reset' : 'start game'}</button>
            <p>{gameActive && gameState === 0 ? pNext ? 'your turn. click a column.' : 'the computer is thinking.' : ''}</p>
            <p>{gameState === 1 ? 'You won' : gameState === -1 ? 'the computer won.' : ''}</p>
          </div>
          <Board 
              grid={grid}
              onClick={i => handleClick(i)}
          />
        </div>
      </div>
    );
}

function createGrid() {
    let x = new Array(7);
    for(let i = 0; i < x.length; i++) {
      x[i] = new Array(6).fill(null);
    }
    return x;
}

function checkFields(a, b, c, d) {
  if (a && a === b && b === c && c === d) {
    return a;
  }
  return null;
}

function gameOver(grid) {
  // check rows
  for (let j = 0; j < 6; j++) {
    for (let i = 0; i < 4; i++) {
      let winner = checkFields(grid[i][j], grid[i+1][j], grid[i+2][j], grid[i+3][j]);
      if(winner) return winner;
    }
  }

  //check columns
  for (let i = 0; i < 7; i++) {
    for (let j = 0; j < 3; j++) {
      let winner = checkFields(grid[i][j], grid[i][j+1], grid[i][j+2], grid[i][j+3]);
      if (winner) return winner;
    }
  }

  //diagonals top left to bottom right
  for (let i = 0; i < 4; i++) {
    for (let j = 0; j < 3; j++) {
      let winner = checkFields(grid[i][j], grid[i+1][j+1], grid[i+2][j+2], grid[i+3][j+3]);
      if (winner) return winner;
    }
  }

  //diagonals top right to bottom
  for (let i = 3; i < 7; i++) {
    for (let j = 0; j < 3; j++) {
      let winner = checkFields(grid[i][j], grid[i-1][j+1], grid[i-2][j+2], grid[i-3][j+3]);
      if (winner) return winner; 
    }
  }
  return 0;
}
